import { Component, h, State, Prop, Listen } from "@stencil/core";

@Component({
    tag: "wxcc-video-modal",
    styleUrl: "wxcc-video-modal.css",
    shadow: true
})


export class wxccVideoModal {
    @State() vidBig = true
    @State() btnLable = "Bigger"
    @Prop({ mutable: true, reflect: true }) vidId: string
    @State() backDrop = false
    @State() hide = false
    boxxie: HTMLElement

    onToggleSize() {
        this.vidBig = !this.vidBig
        // this.btnLable =  this.vidBig ? "Smaller": "Bigger"

    }
    onToggleHide() {
        this.hide = !this.hide
    }

    // backDrop(event: Event){
    // this.boxxie.classList.add()
    // }

    @Listen('vidPop',{target: 'body'})
    onVidPop(event: CustomEvent){
        console.log(event)
        if(event.detail){
            console.log(event)
            this.vidId = event.detail
        }
        this.hide = false
    }

    moveIt(event: Event) {
        event.preventDefault()
        console.log(event)
        console.log(this.boxxie.offsetTop)
        console.log(this.boxxie.offsetLeft)
        this.boxxie.style.top = Math.abs(event["clientY"]) + "px"
        this.boxxie.style.left = Math.abs(event["clientX"]) + "px"
        this.backDrop = false
    }


    render() {
        let url = `https://app.vidcast.io/share/embed/${this.vidId}`
        this.btnLable = this.vidBig ? "Smaller" : "Bigger"
        return [
            <div id="cover" class={this.backDrop ? "covered" : ""}></div>,
            <div draggable id="my-div" ref={el => this.boxxie = el} onDragStart={() => { this.backDrop = true }} onDragEnd={this.moveIt.bind(this)} class={(this.vidBig ? "bigBoi" : "") + (this.hide ? " hidden" : "")}>
                <div id="my-div-header"><span class="hideMe" onClick={this.onToggleHide.bind(this)}>Hide</span>Click here to move <button class="sizeBar" onClick={this.onToggleSize.bind(this)}>{this.btnLable}</button>
                </div>
                <iframe src={url} height="100%" width="100%" frameborder="0" loading="lazy"></iframe>
            </div>

        ]
    }
}